import React, { useState, useEffect } from 'react';

const NavBar = () => {
  const [menuVisible, setMenuVisible] = useState(false);

  const handleToggleMenu = () => {
    setMenuVisible((prevState) => !prevState);
  };

  useEffect(() => {
    const handleListItemClick = () => {
      setMenuVisible(false);
    };

    const listItems = document.querySelectorAll('.nav-list-item');
    listItems.forEach((item) => {
      item.addEventListener('click', handleListItemClick);
    });

    return () => {
      listItems.forEach((item) => {
        item.removeEventListener('click', handleListItemClick);
      });
    };
  }, []);

  return (
    <header>
      <div className="mobile-toggle">
        <button type="button" onClick={handleToggleMenu} aria-label="Toggle Menu">
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
        </button>
      </div>
      <nav className={`nav-menu ${menuVisible ? 'visible' : ''}`}>
        <button type="button" onClick={handleToggleMenu}>
          X
        </button>
        <ul>
          <li className="nav-list-item"><a href="#home">HOME</a></li>
          <li className="nav-list-item"><a href="#work">ADD BUSINESS</a></li>
          <li className="nav-list-item"><a href="#projects">ABOUT US</a></li>
          <li className="nav-list-item"><a href="#blog">CONTACT US</a></li>
        </ul>
      </nav>
    </header>
  );
};

export default NavBar;
